'use client';

import { useEffect, useMemo, useState } from 'react';
import type { Job } from '@/lib/api';
import { searchJobs } from '@/lib/api';

export default function SearchPage() {
  const [q, setQ] = useState('');
  const [rows, setRows] = useState<Job[]>([]);
  const [loading, setLoading] = useState(false);
  const [err, setErr] = useState<string | null>(null);

  // simple debounce
  const debounced = useDebounced(q, 300);

  useEffect(() => {
    let cancelled = false;
    async function load() {
      setLoading(true);
      setErr(null);
      try {
        // searchJobs() already normalizes the output to { rows: [...] }
        const res = await searchJobs(debounced.trim());
        if (!cancelled) setRows(res.rows || []);
      } catch (e: any) {
        if (!cancelled) {
          setErr(e?.message || 'Search failed');
          setRows([]);
        }
      } finally {
        if (!cancelled) setLoading(false);
      }
    }
    load();
    return () => { cancelled = true; };
  }, [debounced]);

  return (
    <main>
      <h1>Search</h1>
      <p className="muted" style={{ marginTop: 4, marginBottom: 12 }}>
        Search by <b>tag</b>, <b>name</b>, <b>phone</b>, or status text.
      </p>

      <div className="card" style={{ padding: 12, marginBottom: 16 }}>
        <form
          onSubmit={(e) => e.preventDefault()}
          style={{ display: 'flex', gap: 8, alignItems: 'center' }}
        >
          <input
            value={q}
            onChange={(e) => setQ(e.target.value)}
            placeholder="e.g. 12345 or Jane Doe or (555) 123-4567"
            aria-label="Search query"
            style={{ flex: 1 }}
          />
          <button className="btn" onClick={() => setQ(q.trim())}>Search</button>
        </form>
        <div style={{ marginTop: 8, fontSize: 12 }}>
          <span className="muted">Shortcuts:</span>{' '}
          <code>@report</code> (ready to call) · <code>@recall</code> (already called)
        </div>
      </div>

      {loading && <div className="card">Loading…</div>}
      {err && <div className="card" style={{ borderColor: '#ef4444' }}>Error: {err}</div>}

      {!loading && !err && (
        <div className="card" style={{ padding: 0 }}>
          <table className="table" style={{ width: '100%' }}>
            <thead>
              <tr>
                <th style={{ width: 110 }}>Tag</th>
                <th>Customer</th>
                <th style={{ width: 160 }}>Phone</th>
                <th style={{ width: 140 }}>Drop-off</th>
                <th>Status</th>
                <th>Caping</th>
                <th>Webbs</th>
              </tr>
            </thead>
            <tbody>
              {rows.length === 0 && (
                <tr>
                  <td colSpan={7} style={{ padding: 14 }}>No results.</td>
                </tr>
              )}
              {rows.map((r) => (
                <tr key={r.tag}>
                  <td><strong>{r.tag}</strong></td>
                  <td>{r.customer || '—'}</td>
                  <td>{r.phone || '—'}</td>
                  <td>{r.dropoff || '—'}</td>
                  <td>{r.status || '—'}</td>
                  <td>{r.capingStatus || '—'}</td>
                  <td>{r.webbsStatus || '—'}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </main>
  );
}

/* ---- tiny hook: debounce any string value ---- */
function useDebounced(value: string, delay = 300) {
  const [v, setV] = useState(value);
  useEffect(() => {
    const t = setTimeout(() => setV(value), delay);
    return () => clearTimeout(t);
  }, [value, delay]);
  return v;
}

